/**
 * Basic typography style for copy text
 */
body {
  color: #333;
  font-size: var(--text-base-size, 1em);
  font-family: var(--font-primary, sans-serif);
}

/**
 * Basic styles for links
 */
a {
  color: #ef3451;
  text-decoration: none;
  transition: all 0.3s linear;
}

a:hover {
  color: #de1231;
  text-decoration: underline;
}

a:hover, a:focus, a:active {
  outline: none;
  color: #ef3451;
}

a:focus, a:active {
  text-decoration: none !important;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-family: -apple-system, BlinkMacSystemFont, "FuturaBook", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  margin-bottom: .67em;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
  line-height: var(--heading-line-height, 1.2);
}

h1, .h1 {
  font-size: var(--text-xxl, 2.074em);
}

h2, .h2 {
  font-size: var(--text-xl, 1.528em);
}

h3, .h3 {
  font-size: var(--text-lg, 1.44em);
}

h4, .h4 {
  font-size: var(--text-md, 1.2em);
}

h5, .h5 {
  font-size: 15px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
}

small {
  font-size: var(--text-xs, 0.833em);
}

p a,
.blog-content p a,
.read-more,
.text-link:not(.btn-block) {
  outline: none;
  position: relative;
  text-decoration: none;
  z-index: 1;
  color: #000;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #fb5762), to(#fb5762));
  background-image: -webkit-linear-gradient(left, #fb5762 0, #fb5762 100%);
  background-image: -o-linear-gradient(left, #fb5762 0, #fb5762 100%);
  background-image: linear-gradient(to right, #fb5762 0, #fb5762 100%);
  background-repeat: no-repeat;
  background-size: 100% .1em;
  background-position: 0 88%;
  -webkit-transition: background-size .25s ease-in;
  -o-transition: background-size .25s ease-in;
  transition: background-size .25s ease-in;
}

p a:hover,
.blog-content p a:hover,
.read-more:hover,
.text-link:not(.btn-block):hover {
  color: #000;
  text-decoration: none;
  background-size: 100% 2em;
}

.text-link.btn-block-styled {
  padding: 10px 75px 10px 20px !important;
  border: 0 none;
  box-shadow: 0 1px 4px rgba(146, 157, 162, 0.5);
  transform-origin: left;
  transition: background 1s ease;
  border-radius: 6px;
  position: relative;
  color: #333;
  margin-bottom: 11px;
}

.text-link.btn-block-styled:after {
  background: #ef3451;
  position: absolute;
  right: 0px;
  top: 0px;
  height: 100%;
  content: "➜";
  color: white;
  line-height: 100%;
  transform-origin: left center;
  text-align: center;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  transition: width 0.3s ease 0s;
  border-radius: 0px 6px 6px 0px;
}

.text-link.btn-block-styled:hover {
  box-shadow: 0 1px 10px rgba(146, 157, 162, 0.5);
  color: #ef3451;
  text-decoration: none;
}

.text-link.btn-block-styled:hover:after {
  width: 65px;
}

.dark-theme .read-more {
  text-decoration: none;
  background-size: 100% 1em;
  color: #2d2d2d !important;
}

.dark-theme .read-more:hover {
  color: #fff !important;
  background-size: 100% .2em;
}

.text-link.btn-block {
  padding: 10px 75px 10px 20px !important;
  margin-bottom: 11px;
  position: relative;
  letter-spacing: 1px;
}

.text-link.btn-block.active:after, .text-link.btn-block:hover:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 0.25rem;
  background: #ef3451;
}

/**
 * Clear inner floats
 */
.clearfix::after {
  clear: both;
  content: '';
  display: table;
}

/**
 * Main content containers
 * 1. Make the container full-width with a maximum width
 * 2. Center it in the viewport
 * 3. Leave some space on the edges, especially valuable on small screens
 */
.wrapper {
  max-width: 1540px;
  /* 1 */
  margin-left: auto;
  /* 2 */
  margin-right: auto;
  /* 2 */
  padding-left: 20px;
  /* 3 */
  padding-right: 20px;
  /* 3 */
  width: 100%;
  /* 1 */
  position: relative;
}

/**
 * Hide text while making it readable for screen readers
 * 1. Needed in WebKit-based browsers because of an implementation bug;
 *    See: https://code.google.com/p/chromium/issues/detail?id=457146
 */
.hide-text {
  overflow: hidden;
  padding: 0;
  /* 1 */
  text-indent: 101%;
  white-space: nowrap;
}

/**
 * Hide element while making it readable for screen readers
 * Shamelessly borrowed from HTML5Boilerplate:
 * https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133
 */
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

